@import '../components/content/dropdown-filters';
@import '../components/content/datatables';

.front-page {
    display: flex;
    flex-direction: column;
    min-height: 100vh;

    &.ua-mobile {
        margin-top: $size__navbar-height;
    }

    #page {
        flex: 1 0 auto;

        .team-filters {
            justify-content: space-evenly;
            margin: {
                top: 0.5em;
                bottom: 0.5em;
            }

            @include breakpoint(medium) {
                margin-top: 0;
            }

            label {
                span {
                    opacity: 0.6;
                }

                &.active {
                    span {
                        opacity: 1;
                    }
                }
            }
        }

        #all-matches {
            font-size: 0.875em;

            tbody {
                th,
                td {
                    height: rem-calc(36px);
                }

                td {
                    &.venue {
                        @include text(right);
                    }
                }
            }

            @include breakpoint(medium) {
                font-size: 0.9375em;
            }

            @include breakpoint(large) {
                font-size: 1em;
            }

            .icon {
                height: 28px;
            }

            [src*='argentina-xv'],
            [src*='victoria-rugby'],
            [src*='south-australia'] {
                height: 18px;
            }

            &_wrapper {
                .wpcm-row:last-child {
                    .paging_full_numbers {
                        margin: auto;
                    }

                    .wpcm-column {
                        width: 100%;
                    }
                }
            }

            &_filter {
                padding-left: 0.55%;
            }
        }
    }
}
